<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row mb-4">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
            <div class="row mt-5 mb-5 pb-4 mx-auto" v-if="loading">
                <div class="col-12 p-5 m-4 text-center">
                    <div class="spinner-border text-secondary" role="status" v-if="loading">
                        <span class="sr-only">Loading..</span>
                      </div>
                </div>
            </div>
            <div v-if="user_type == 'su' || user_type == 'adm'">
              <div class="row mb-0" v-if="!loading">
                  <div class="col-5 col-xl-7">
                    <div class="">
                      <b-button
                          @click="addRegionModal"
                          variant="success"
                          class="btn mr-1"
                        >
                          {{ $t("button.add.text") }}
                          <i class="mdi mdi-plus mr-1"></i>
                        </b-button>

                      <b-modal
                            id="modal-add"
                            centered
                            bordered
                            :title="$t('button.add.text')"
                            title-class="font-18"
                            :ok-title="$t('button.save.text')"
                            :cancel-title="$t('button.cancel.text')"
                            @hidden="resetForm()"
                          >
                            <div class="row">
                              <div class="col-md-6">
                                <div class="form-group">
                                  <label for="validationCustom01">{{
                                    $t("region.region-name")
                                  }}</label>
                                  <input
                                    id="validationCustom01"
                                    type="text"
                                    class="form-control"
                                    :placeholder="
                                      $t('region.region-name')
                                    "
                                    v-model="form.region_name"
                                    :class="{
                                      'is-invalid': submitted && $v.form.region_name.$error,
                                    }"
                                  />
                                  <div
                                    v-if="submitted && $v.form.region_name.$error"
                                    class="invalid-feedback"
                                  >
                                    <span v-if="!$v.form.region_name.required">{{
                                      $t("form.error.text")
                                    }}</span>
                                  </div>
                                </div>
                              </div>  

                              <div class="col-md-6">
                                <div class="form-group">
                                  <label for="validationCustom01">{{
                                    $t("region.region-number")
                                  }}</label>
                                  <input
                                    id="validationCustom01"
                                    type="text"
                                    class="form-control"
                                    :placeholder="
                                      $t('region.region-number')
                                    "
                                    v-model="form.region_number"
                                    :class="{
                                      'is-invalid': submitted && $v.form.region_number.$error,
                                    }"
                                  />
                                  <div
                                    v-if="submitted && $v.form.region_number.$error"
                                    class="invalid-feedback"
                                  >
                                    <span v-if="!$v.form.region_number.required">{{
                                      $t("form.error.text")
                                    }}</span>
                                  </div>
                                </div>
                              </div>   

                              <div class="col-md-12">
                                <hr>
                              </div>

                              <div class="col-md-12">
                                <div class="form-group">
                                  <label for="validationCustom01">
                                    Province
                                  </label>
                                  <select class="custom-select" v-model="location.province" @change="changeProvince">
                                    <option value="0">Select Province</option>
                                    <option v-for="prov in province" :key="prov.code" :value="prov.code">{{prov.name}}</option>
                                  </select>
                                  <div
                                    v-if="submitted && $v.form.region_number.$error"
                                    class="invalid-feedback"
                                  >
                                    <span v-if="!$v.form.region_number.required">{{
                                      $t("form.error.text")
                                    }}</span>
                                  </div>
                                </div>
                              </div> 

                              <div class="col-md-12">
                                <div class="form-group">
                                  <label for="validationCustom01">
                                    District
                                  </label>
                                  <select class="custom-select" v-model="location.district" @change="changeDistrict" :disabled="disabled.district">
                                    <option value="0">Select District</option>
                                    <option v-for="dist in district" :key="dist.code" :value="dist.code">{{dist.name}}</option>
                                  </select>
                                  <div
                                    v-if="submitted && $v.form.region_number.$error"
                                    class="invalid-feedback"
                                  >
                                    <span v-if="!$v.form.region_number.required">{{
                                      $t("form.error.text")
                                    }}</span>
                                  </div>
                                </div>
                              </div> 
                              
                              <div class="col-md-12">
                                <div class="form-group">
                                  <label for="validationCustom01">
                                    Regency
                                  </label>
                                  <select class="custom-select" v-model="location.regency" @change="changeRegency" :disabled="disabled.regency">
                                    <option value="0">Select Regency</option>
                                    <option v-for="reg in regency" :key="reg.code" :value="reg.code">{{reg.name}}</option>
                                  </select>
                                  <div
                                    v-if="submitted && $v.form.region_number.$error"
                                    class="invalid-feedback"
                                  >
                                    <span v-if="!$v.form.region_number.required">{{
                                      $t("form.error.text")
                                    }}</span>
                                  </div>
                                </div>
                              </div> 
                              
                              <div class="col-md-12">
                                <div class="form-group">
                                  <label for="validationCustom01">
                                    Sub Regency
                                  </label>
                                  <select class="custom-select" v-model="location.subregency" @change="changeSubRegency" :disabled="disabled.subregency">
                                    <option value="0">Select Sub Regency</option>
                                    <option v-for="subreg in subregency" :key="subreg.code" :value="subreg.code">{{subreg.name}}</option>
                                  </select>
                                  <div
                                    v-if="submitted && $v.form.region_number.$error"
                                    class="invalid-feedback"
                                  >
                                    <span v-if="!$v.form.region_number.required">{{
                                      $t("form.error.text")
                                    }}</span>
                                  </div>
                                </div>
                              </div> 

                            </div>
                              <template #modal-footer="{ cancel }">
                                <b-button size="md" variant="danger" @click="cancel()">
                                  {{$t('button.cancel.text')}}
                                </b-button>
                                <b-button size="md" variant="success" @click="addRegions">
                                  {{$t('button.save.text')}}
                                </b-button>
                              </template>
                        </b-modal>

                        <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border rounded" variant="white"  ref="dropdown">
                          <template slot="button-content">
                            {{ $t('table.show-label') }}
                            <i class="mdi mdi-chevron-down ml-2"></i>
                          </template>
                          <span v-for="val in pageOptions" :key="val">
                              <button class="dropdown-item" @click="perPageSelect(val)">
                              <span class="text-success" v-if="val == perPage"><b> {{val}} </b></span>
                              <span v-else>{{val}}</span>
                              </button>
                          </span>
                        </b-dropdown>
                        <b-dropdown class="btn-group mr-2 mb-2 mb-sm-0 border" variant="white" v-if="selected[0]">
                          <template slot="button-content">
                            {{ $t('button.bulk-action') }}
                            <i class="mdi mdi-chevron-down ml-2"></i>
                          </template>
                          <button class="dropdown-item" @click="bulkAction()">{{ $t('button.hapus.text') }}</button>
                        </b-dropdown>
                    </div>
                </div>
                <div class="col-4 col-xl-3">
                </div>
                  <div class="col-3 col-xl-2">
                    <div class="form-group has-search">
                      <span class="bx bx-search-alt search-icon form-control-feedback"></span>
                        <b-form-input
                        v-model="filter"
                        type="search"
                        :placeholder="$t('navbar.search.text')"
                        class="form-control"
                      ></b-form-input>
                    </div>
                </div>
                <!-- end col-->
              </div>
            </div>

            <!-- Table -->
            <div class="table-responsive mb-0" :class="{ 'pb-5' : rows < 10 }" v-if="user_type == 'su' || user_type == 'adm'">
              <div v-if="!loading">
                <b-table
                :items="tableData"
                :fields="fields"
                bordered
                striped
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                <template #head(bulk) width="4%">
                    <b-form-checkbox
                            v-model="selectAll" 
                            @change="select"
                            class="text-center mt-0">
                    </b-form-checkbox>
                </template>
                <template #cell(bulk)="row" width="4%">
                <b-form-checkbox
                  class="text-center mt-0"
                  v-model="selected"
                  :value="row.item.id_region"
                >
                </b-form-checkbox>
                </template>
  
                <template #cell(location)="row">
                  <span class="text-capitalize">
                    {{row.item.subregency}}, {{row.item.regency}}, {{row.item.district}}, {{row.item.prov}}
                  </span>
                </template>
                <template #cell(actions)="row">
                      <b-button class="btn btn-sm mr-1" variant="info" @click="editRegion(row.item.id_region, row.item.region_name, row.item.region_number, row.item.id_district)"><i class="bx bx-pencil"></i> 
                      </b-button>
                       <b-modal
                        ref="ref-modal-edit"
                        :id="'modal-edit' + row.item.id_region"
                        centered
                        :title="$t('button.update.text')"
                        title-class="font-17"
                        :ok-title="$t('button.update.text')"
                        :cancel-title="$t('button.cancel.text')"
                        @hidden="resetForm()"
                      >
                          <div class="row">
                            <div class="col-md-6">
                              <div class="form-group">
                                <label for="validationCustom01">{{
                                  $t("region.region-name")
                                }}</label>
                                <input
                                  id="validationCustom01"
                                  type="text"
                                  class="form-control"
                                  :placeholder="
                                    $t('region.region-name')
                                  "
                                  v-model="form.region_name"
                                  :class="{
                                    'is-invalid': submitted && $v.form.region_name.$error,
                                  }"
                                />
                                <div
                                  v-if="submitted && $v.form.region_name.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.region_name.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div>   
                             <div class="col-md-6">
                              <div class="form-group">
                                <label for="validationCustom02">{{
                                  $t("region.region-number")
                                }}</label>
                                <input
                                  id="validationCustom01"
                                  type="text"
                                  class="form-control"
                                  :placeholder="
                                    $t('region.region-number')
                                  "
                                  v-model="form.region_number"
                                  :class="{
                                    'is-invalid': submitted && $v.form.region_number.$error,
                                  }"
                                />
                                <div
                                  v-if="submitted && $v.form.region_number.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.region_number.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div> 
                            <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom01">
                                  Province
                                </label>
                                <select class="custom-select" v-model="location.province" @change="changeProvince">
                                  <option value="0">Select Province</option>
                                  <option v-for="prov in province" :key="prov.code" :value="prov.code">{{prov.name}}</option>
                                </select>
                                <div
                                  v-if="submitted && $v.form.region_number.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.region_number.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div> 

                            <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom01">
                                  District
                                </label>
                                <select class="custom-select" v-model="location.district" @change="changeDistrict" :disabled="disabled.district">
                                  <option value="0">Select District</option>
                                  <option v-for="dist in district" :key="dist.code" :value="dist.code">{{dist.name}}</option>
                                </select>
                                <div
                                  v-if="submitted && $v.form.region_number.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.region_number.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div> 
                            
                            <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom01">
                                  Regency
                                </label>
                                <select class="custom-select" v-model="location.regency" @change="changeRegency" :disabled="disabled.regency">
                                  <option value="0">Select Regency</option>
                                  <option v-for="reg in regency" :key="reg.code" :value="reg.code">{{reg.name}}</option>
                                </select>
                                <div
                                  v-if="submitted && $v.form.region_number.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.region_number.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div> 
                            
                            <div class="col-md-12">
                              <div class="form-group">
                                <label for="validationCustom01">
                                  Sub Regency
                                </label>
                                <select class="custom-select" v-model="location.subregency" @change="changeSubRegency" :disabled="disabled.subregency">
                                  <option value="0">Select Sub Regency</option>
                                  <option v-for="subreg in subregency" :key="subreg.code" :value="subreg.code">{{subreg.name}}</option>
                                </select>
                                <div
                                  v-if="submitted && $v.form.region_number.$error"
                                  class="invalid-feedback"
                                >
                                  <span v-if="!$v.form.region_number.required">{{
                                    $t("form.error.text")
                                  }}</span>
                                </div>
                              </div>
                            </div>
                          </div>
                          <template #modal-footer="{ cancel }">
                              <b-button size="md" variant="danger" @click="cancel()">
                                {{$t('button.cancel.text')}}
                              </b-button>
                              <b-button size="md" variant="success" @click="updateRegions($event, row.item.id_region)">
                                {{$t('button.save.text')}}
                              </b-button>
                            </template>
                      </b-modal>
                      <b-button class="btn btn-sm" variant="danger" @click="deleteRegions(row.item.id_region)"><i class="bx bx-trash"></i> 
                      </b-button>
                </template>
                </b-table>
              </div>
              <div v-if="!tableData[0] && !loading">
                <div  class="row  mt-5 mb-5 pb-4 mx-auto">
                    <div class="col-12  p-5 my-5 text-center">
                        <span class="text-secondary">{{ $t('all.dnf') }}</span>  
                    </div>
                </div>
              </div>
            </div>
            <div class="row" v-if="rows > 10">
              <div class="col-6 text-left">
                <div class="text-muted float-left">
                    {{ $t('table.show-label') }} <b>{{perPage}} </b>{{ $t('table.entri-label') }}
                </div>
              </div>
              <div class="col-6 text-right">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination
                      v-model="currentPage"
                      :total-rows="rows"
                      :per-page="perPage"
                    ></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Vue from "vue";
import Swal from "sweetalert2";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import { required, sameAs } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import RegionService from "@/helpers/region.service";
import store from '@/state/store';
import moment from "moment";

export default {
  page: {
    meta: [{ name: "description", content: appConfig.description }],
  },
  components: { Layout, PageHeader },
  data() {
    return {
      urlImage: process.env.VUE_APP_BASE_API_URL_AVATAR,
      user_type: store.state.authBackend.user.type,
      title: this.$t("region.region-management"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("region.region-management"),
          active: true,
        },
      ],
      loading:false,
      tableData:[],
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortDesc: false,
      selected:[],
      selectAll: false,
      fields: [
        { key: "id_region", thClass: "d-none", tdClass: "d-none" },
         { key: "bulk",  thStyle: { width: "2%" }, label:'#'},
        {
          key: "region_name",
          sortable: true,
          label: this.$t("region.region-name"),
        },
        {
          key: "region_number",  
          thStyle: { width: "15%" },
          sortable: true,
          label: this.$t("region.region-number"),
        },
        {
          key: "location",  
          thStyle: { width: "25%" },
          sortable: true,
          label: "Region Location",
        },
        { 
          key: "created_at", 
          thStyle: { width: "15%" }, 
          sortable: true,
          label: this.$t("all.created-at"),
        },
        {
          key: "actions", 
          thStyle: { width: "15%" },
          class: "text-center",
          label: this.$t("button.action"),
        },
      ],
      form: {
          "region_name" : "",
          "region_number" : "",
          "id_district" : "",
      },
      submitted: false,
      province: [],
      district: [],
      regency: [],
      subregency: [],
      location: {
        province : "0",
        district : "0",
        regency : "0",
        subregency : "0",
      },
      disabled: {
        district : true,
        regency : true,
        subregency : true,
      }
    };
  },
  validations: {
    form: {
        region_name: { required },
        region_number: { required },
        id_district: { required }
    },
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
  },
  methods: {
    select() {
        this.selected = [];
        if (!this.selectAll) {
            for (let i in this.tableData) {
                this.selected.push(this.tableData[i].id_region);
            }
        }
    },
    list() {
      // this.loading = true;
      UserService.getProfile()
      .then(response => {
        // console.log( response.data.data)
        this.user_type = response.data.data.admin.role
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },
    getProvince(){
      RegionService.getProvince()
      .then(response => {
        this.province = response.data.data;
        console.log('pp => ',response);
      })
      .catch(err => {
        console.log('er => ',err);
      })
    },
    changeProvince(){
      if (this.location.province != 0) {
        this.location.district = "0";
        this.changeDistrict();
        this.disabled.regency = true;
        this.location.regency = "0";
        this.changeRegency();
        this.disabled.subregency = true;
        this.location.subregency = "0";
        this.changeSubRegency();
        this.disabled.district = false;
        this.getDistrict();
      }else{
        this.disabled.district = true;
        this.location.district = "0";
        this.changeDistrict();
        this.disabled.regency = true;
        this.location.regency = "0";
        this.changeRegency();
        this.disabled.subregency = true;
        this.location.subregency = "0";
        this.changeSubRegency();
      }

      console.log('change province =>', this.location.province);
    },
    getDistrict(){
      const data = {
        "id" : this.location.province
      }
      RegionService.getDistrict(data)
      .then(response => {
        this.district = response.data.data;
        this.disabled.district = false;
        console.log('pd => ',response);
      })
      .catch(err => {
        console.log('er => ',err);
      })
    },
    changeDistrict(){
      if (this.location.district != 0) {
        this.location.regency = "0";
        this.changeRegency();
        this.disabled.subregency = true;
        this.location.subregency = "0";
        this.changeSubRegency();
        this.disabled.regency = false;
        this.getRegency();
      }else{
        this.disabled.regency = true;
        this.location.regency = "0";
        this.changeRegency();
        this.disabled.subregency = true;
        this.location.subregency = "0";
        this.changeSubRegency();
      }

      console.log('change province =>', this.location.district);
    },
    getRegency(){
      const data = {
        "id" : this.location.district
      }
      RegionService.getRegency(data)
      .then(response => {
        this.regency = response.data.data;
        this.disabled.regency = false;
        console.log('pr => ',response);
      })
      .catch(err => {
        console.log('er => ',err);
      })
    },
    changeRegency(){
      if (this.location.regency != 0) {
        this.location.subregency = "0";
        this.changeSubRegency();
        this.disabled.subregency = false;
        this.getSubRegency();
      }else{
        this.disabled.subregency = true;
        this.location.subregency = "0";
        this.changeSubRegency();
      }

      console.log('change province =>', this.location.regency);
    },
    getSubRegency(){
      const data = {
        "id" : this.location.regency
      }
      RegionService.getSubRegency(data)
      .then(response => {
        this.subregency = response.data.data;
        this.disabled.subregency = false;
        console.log('psr => ',response);
      })
      .catch(err => {
        console.log('er => ',err);
      })
    },
    changeSubRegency(){
      if (this.location.subregency != 0) {
        this.form.id_district = this.location.subregency;
      }else{
        this.form.id_district = "";
      }

      console.log('change province =>', this.location.subregency);
    },

    getRegion() {
      this.loading = true;
      RegionService.getAllRegion()
        .then((response) => {
          this.tableData = [];
          console.log(response.data.data);
          response.data.data.forEach((val) => {
            const data = {
              id_region: val.id_region,
              region_name: val.region_name,
              region_number: val.region_number,
              id_district: val.id_district,
              prov: val.prov,
              district: val.district,
              regency: val.regency,
              subregency: val.subregency,
        
              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at),
            };
            this.tableData.push(data);
            this.loading = false;
          });
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    getRegionUpdate() {
      RegionService.getAllRegion()
        .then((response) => {
          this.tableData = [];
          // console.log(response.data.data);
          response.data.data.forEach((val) => {
            const data = {
              id_region: val.id_region,
              region_name: val.region_name,
              region_number: val.region_number,
              id_district: val.id_district,
              prov: val.prov,
              district: val.district,
              regency: val.regency,
              subregency: val.subregency,

              created_at: this.formatDate(val.created_at),
              updated_at: this.formatDate(val.updated_at)
            };
            this.tableData.push(data);
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    addRegionModal(){
        this.$bvModal.show('modal-add')
        this.form.region_name = "";
        this.form.region_number = "";
    },
    addRegions(e) {
      // this.loadingAdd = true;
      this.submitted = true;
      this.$v.form.$touch();
      if (
        this.form.region_name == ""  || 
        this.form.region_number == "" ||
        this.form.id_district == "" 
      ) {
        e.preventDefault();
      } else {
        const data = {
          region_name: this.form.region_name,
          region_number: this.form.region_number,
          id_district: this.form.id_district,
        };
        console.log(data);
        RegionService.addRegion(data)
          .then((response) => {
            Vue.swal({
              position: "top-end",
              icon: "success",
              title: response.data.message,
              showConfirmButton: false,
              timer: 1500,
            });
            this.resetForm();
            this.$v.$reset();
            this.$bvModal.hide('modal-add');
            this.getRegionUpdate();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    editRegion(id_region, region_name, region_number, id_district) {
      // console.log(id_region);
      // console.log('dist ', id_district.split("."));
      const district = id_district.split(".");
      this.$bvModal.show("modal-edit" + id_region);
      this.form.region_name = region_name;
      this.form.region_number = region_number;
      this.form.id_district = id_district;
      this.location.province = district[0];
      this.location.district = district[0]+"."+district[1];
      this.location.regency = district[0]+"."+district[1]+"."+district[2];
      this.location.subregency = id_district;
      this.getProvince();
      this.getDistrict();
      this.getRegency();
      this.getSubRegency();
    },

    updateRegions(e, id) {
      this.submitted = true;
      this.$v.form.$touch();
      if ( this.form.region_name == "" || this.form.region_number == "") {
        e.preventDefault();
      } else {
        let data = {
              "region_name" : this.form.region_name,
              "region_number" : this.form.region_number,
              "id_district" : this.form.id_district,
        }
        // console.log(data);
        RegionService.updateRegion(id, data)
          .then((response) => {
            let id_region = response.data.data.id_region;
            // console.log(id_region);
            Vue.swal({
              position: "top-end",
              icon: "success",
              title: response.data.message,
              showConfirmButton: false,
              timer: 1500,
            });
            this.$bvModal.hide("modal-edit" + id_region);
            this.getRegionUpdate();
            this.resetForm();
            this.$v.$reset();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    deleteRegions(id) {
      Swal.fire({
        title: this.$t("button.delete.modal.confirmText[0]"),
        text: this.$t("button.delete.modal.confirmText[1]"),
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#34c38f",
        cancelButtonColor: "#f46a6a",
        cancelButtonText: this.$t("button.cancel.text"),
        confirmButtonText: this.$t("button.delete.text"),
      }).then((result) => {
        if (result.isConfirmed) {
          // console.log(id);
          RegionService.deleteRegion(id)
            .then((response) => {
              this.getRegionUpdate();
              Swal.fire({
                position: "top-end",
                icon: "success",
                title:  response.data.message,
                showConfirmButton: false,
                timer: 1500,
              });
            })
            .catch((err) => {
              console.log(err);
            });
        }
      });
    },
    bulkAction(){
          Swal.fire({
              title: this.$t('button.delete.modal.confirmText[0]'),
              text: this.$t('button.delete.modal.confirmText[1]'),
              icon: "warning",
              showCancelButton: true,
              confirmButtonColor: "#34c38f",
              cancelButtonColor: "#f46a6a",
              cancelButtonText: this.$t('button.cancel.text'),
              confirmButtonText: this.$t('button.delete.text')
          }).then((result) => {
              if (result.isConfirmed) {
                var data = this.selected;
                // console.log(data);
                  RegionService.bulkdeleteRegion(data)
                  .then( response => {
                      this.selected = [];
                      this.getRegionUpdate();
                      Swal.fire({
                          position: "top-end",
                          icon: "success",
                          title:  response.data.message,
                          showConfirmButton: false,
                          timer: 1500
                      });
                  })
                  .catch( err => {
                      console.log(err);
                  });
              }
          });
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    onDelete(id) {
      console.log("delete data:", id);
    },
    onView(id) {
      console.log("details data:", id);
    },
    perPageSelect(val){
      this.perPage = val;
      console.log(this.perPage);
      this.$refs.dropdown.hide();
    },
    resetForm() {
      // setTimeout(() => {
        this.form.region_name = "";
        this.form.region_number = "";
        this.location.province = 0;
        this.changeProvince();
      // }, 500);
    },
    formatDate(value) {
      return moment(value).format("YYYY-MM-DD, hh:mm");
    },
  },
  beforeMount(){
    this.list();
  },
  mounted() {
    console.log('loc', location.province);
    this.totalRows = this.items.length;
    this.getRegion();
    this.getProvince();
  },
};
</script>

<style scoped>
.badge {
  border-radius: 2%;
}

